<template>
    <div class="webshop-integral">
        <v-classify ref="classify" :details-data="detailsData" :gtype="gtype"></v-classify>
    </div>
</template>

<script>
import { mapState } from "vuex";
import vClassify from "./webShopClassify.vue";
import rectGoodItem from "./rectGoodItem.vue";

export default {
  name: "v-webshop-integral",
  data() {
    return {
      detailsData: [],
      isLoadDb: false,
      memoryScroll: 0,
      gtype:2
    };
  },
  components: { vClassify,rectGoodItem },
  methods: {
    LoadData() {
      this.$http
        .post(this.$store.state.path + "php/Route.php", {
          type:'topgoods',
          gtype:this.gtype
        })
        .then(response => {
          if (response.body.error == "ok") {
            this.$store.dispatch("toggleLoading", false);
            this.isLoadDb = true;
            this.detailsData=response.body.arr;
          }
        })
        .catch(response=>{
          this.$store.dispatch("toggleLoading", false);
        });
    },
  },
  activated() {
    document.body.scrollTop = this.memoryScroll;
    if (this.isLoadDb) this.$store.dispatch("toggleLoading", false);
    this.$refs.classify.activedHandle();
  },
  mounted() {
    this.LoadData();
  },
  beforeRouteLeave(to, from, next) {
    this.$refs.classify.disactivedHandle();
    this.memoryScroll = document.body.scrollTop;
    next();
  },
  computed: {
    ...mapState(["path", "icon","userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.webshop-integral{
  padding-bottom: 0.4rem;
}
</style>



