<template>
    <div class="web-shop-classify">
        <div class="swiper-section">
            <div class="swipe-wrapper">
                <mt-swipe ref="swiperComponent" :auto="4000">
                    <mt-swipe-item v-for="site in detailsData">
                        <my-image class="my-img" :imgUrl="path+site.topicon" @click.native="clickDetailsHandle(site)"></my-image>
                    </mt-swipe-item>
                </mt-swipe>
            </div>
        </div>
        <div ref="menuSection" class="menu-section">
          <div class="menu-placeholder"></div>
          <order-menu class="order-menu" :gtype="gtype"></order-menu>
        </div>
        <div ref="goodsSection" class="goods-section">
          <ul class="webshop-rect-list" v-infinite-scroll="requestData" infinite-scroll-disabled="cellLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
            <li class="webshop-rect-list-li" v-for="cellData in goodsList" @click="clickDetailsHandle(cellData)">
              <rect-good-item  :good-item="cellData"></rect-good-item>
            </li>
          </ul>
          <div class="tip-div">
            <img v-show="tipWord=='加载中 ...'" class="img" src="../../../assets/loading.gif">
            <div v-show="tipWord!='加载中 ...'" class="line left-line"></div>
            <p class="load-tip">{{tipWord}}</p>
            <div v-show="tipWord!='加载中 ...'" class="line right-line"></div>
          </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { Swipe, SwipeItem, InfiniteScroll } from "mint-ui";
import Vue from "vue";
import "mint-ui/lib/swipe/style.css";
import "mint-ui/lib/swipe-item/style.css";
import rectGoodItem from "./rectGoodItem.vue";
import myImage from "../../lazyloadImg/lazyImage.vue";
import utils from "../../../common/js/utils.js";
import orderMenu from "./orderMenu.vue";

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.use(InfiniteScroll);

export default {
  name: "v-classify",
  props: {
    detailsData: null,
    gtype: 0 //gtype 商品类型    -1 所有  0.特价  1.推荐  2.积分商品   10.优惠套餐 11.合作商铺   20.项目引进  21.合作品牌
  },
  data() {
    return {
      orderMenuDom: null,
      fixedY: 0,
      ranktp: 1, //排行类型  1综合  2 价格  3销量   不参与写0
      lift: 0, //排序规则 1升序  2降序	不参与写0
      page: 0, //分页	不参与写0    page从0页开始
      cellLoading: true,
      pageNum: 10,
      goodsList: [],
      tipWord: "加载中 ...",
      existIdArr: [] //保存已经显示的商品id
    };
  },
  created() {
    //防止无限加载初始化的时候多次加载
    this.requestData(false);
  },
  components: { myImage, rectGoodItem, orderMenu },
  methods: {
    sortGoodsFun0() {
      console.log("综合排序");
      this.orderHandle(1, 0);
    },
    sortGoodsFun1() {
      console.log("销量排序");
      this.orderHandle(3, 2);
    },
    sortGoodsFun2Up() {
      console.log("价格升序");
      this.orderHandle(2, 1);
    },
    sortGoodsFun2Down() {
      console.log("价格降序");
      this.orderHandle(2, 2);
    },
    orderHandle(ranktp, lift) {
      this.goodsList = [];
      this.existIdArr = [];
      this.ranktp = ranktp;
      this.lift = lift;
      this.page = 0;
      this.tipWord = "加载中 ...";
      this.$nextTick(() => {
        var topScroll = document.body.scrollTop; //滚动的距离,距离顶部的距离
        if (topScroll > this.fixedY) {
          document.body.scrollTop = this.fixedY;
        }
      });
      utils.throttle(this.throttleRequest, 300, 1000)();
    },
    throttleRequest() {
      this.requestData(false);
    },
    requestData(infinite = true) {
      this.tipWord = "加载中 ...";
      if (infinite && this.page == 0) return;
      if (infinite && this.cellLoading) return;
      this.cellLoading = true;
      this.$http
        .post(this.$store.state.path + "php/Route.php", {
          type: "goodsbytp",
          gtype: this.gtype,
          ranktp: this.ranktp,
          lift: this.lift,
          page: this.page,
          userid: this.userid
        })
        .then(response => {
          if (response.body.error == "ok") {
            if (!response.body.arr || response.body.arr.length == 0) {
              //数据为空
              this.tipWord = "没有更多的内容了";
              this.cellLoading = true;
            } else {
              //有数据
              if (response.body.arr.length % 10 == 0) {
                //满一页数据
                this.page++;
                this.cellLoading = false;
              } else {
                //不满一页
                this.cellLoading = true;
                this.tipWord = "没有更多的内容了";
              }

              for (let i = 0; i < response.body.arr.length; i++) {
                let goodItem = response.body.arr[i];
                if (this.existIdArr.indexOf(goodItem.id) >= 0) continue;
                this.goodsList.push(goodItem);
                this.existIdArr.push(goodItem.id);
              }

              if (
                !this.$refs.goodsSection ||
                !this.$refs.goodsSection.style.minHeight
              ) {
                this.$nextTick(() => {
                  this.getGoodsSectionMinHeight();
                });
              }
            }
          }
        });
    },
    getGoodsSectionMinHeight() {
      if (!this.$refs.goodsSection) return;
      let h = this.$refs.goodsSection.offsetHeight;
      this.$refs.goodsSection.style.minHeight = h + "px";
    },
    clickDetailsHandle(objs) {
      var mealItem = {};
      mealItem["cardId"] = objs.id + "";
      this.$store.commit("UPDATE_CURMEALDATA", mealItem);
      this.$router.push({ name: "details" });
    },
    activedHandle() {
      this.$refs.swiperComponent.initTimer();
      this.fixedY = this.$refs.menuSection.offsetTop;
      window.addEventListener("scroll", this.onWindowScroll, false);
    },
    disactivedHandle() {
      this.$refs.swiperComponent.clearTimer();
      window.removeEventListener("scroll", this.onWindowScroll);
    },
    onWindowScroll() {
      var topScroll = document.body.scrollTop; //滚动的距离,距离顶部的距离
      if (!this.orderMenuDom)
        this.orderMenuDom = document.querySelector(
          ".web-shop-classify .order-menu"
        );
      // console.log("this.topScroll=" + topScroll, " this.fixedY=" + this.fixedY);
      if (topScroll >= this.fixedY) {
        if (this.orderMenuDom.classList.contains("fixed")) return;
        this.orderMenuDom.classList.add("fixed");
      } else {
        if (!this.orderMenuDom.classList.contains("fixed")) return;
        this.orderMenuDom.classList.remove("fixed");
      }
    }
  },
  mounted() {
    // this.menuItemClick(0);
  },
  computed: {
    ...mapState(["path", "icon", "userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.web-shop-classify {
  .menu-section {
    width: 100%;
    position: relative;

    .menu-placeholder {
      width: 100%;
      height: 0.31rem;
    }

    .order-menu {
      position: absolute;
      top: 0;
      z-index: 3;
      box-shadow: 0 1px 1px rgba(50, 50, 50, 0.15);

      &.fixed {
        position: fixed;
      }
    }
  }

  .goods-section {
    width: 100%;
    margin-top: 0.03rem;

    .tip-div {
      width: 100%;
      height: 0.36rem;
      display: flex;
      align-items: center;
      justify-content: center;

      .img {
        width: 0.22rem;
        height: 0.22rem;
        margin-right: 0.05rem;
      }

      .load-tip {
        margin-top: 0.02rem;
        font-size: 0.12rem;
        color: #707070;
        // letter-spacing: 0.01rem;
      }

      .line {
        flex: 1;
        height: 1px;
        background-color: #eee;
        box-sizing: border-box;

        &.left-line {
          margin-right: 0.15rem;
          margin-left: 0.16rem;
        }

        &.right-line {
          margin-left: 0.15rem;
          margin-right: 0.16rem;
        }
      }
    }

    .webshop-rect-list {
      flex-wrap: wrap;
      display: flex;
      justify-content: space-between;
      margin: 0 0.14rem;
      font-size: 0;

      .webshop-rect-list-li {
        margin-top: 0.08rem;
        flex: 0 1 1.4rem;
        background-color: #fffded;
      }
    }
  }
}
</style>



