<template>
    <div class="rect-good">
        <my-image class="my-img" :imgUrl="path+goodItem.icon"></my-image>
        <div class="content">
            <div class="content-top">
                <div class="good-name">{{goodItem.gname}}</div>
            </div>
            <div class="content-down">
                <div v-if="goodItem.type.toString()=='2'" class="down-left">{{parseInt(goodItem.price)}}<span class="integral">积分</span></div>
                <div v-else class="down-left"><span class="symbol">&yen;</span>{{goodItem.price}}</div>
                <div class="down-right">
                    <p class="sale-word">销量</p>
                    <p class="sale-num">{{goodItem.buynum}}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import myImage from "../../lazyloadImg/lazyImage.vue";

export default {
  name: "rect-good-item",
  data() {
    return {};
  },
  props: {
    goodItem: {
      type: Object,
      default: function() {
        return {
          icon: "",
          price: 0,
          gname: "",
          buynum: 0
        };
      }
    }
  },
  components: { myImage },
  mounted() {
  },
  computed: {
    ...mapState(["openid", "path"])
  },
  methods: {}
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.rect-good {
    width 100%;

    .my-img {
        width: 1.4rem;
        height: 1.4rem;
        background-color #f1f1f1;
    }

    .content {
        margin: 0 0.05rem;
        height: 0.6rem;
        display: flex;
        flex-direction: column;

        .content-top {
            width: 100%;
            flex: 0 1 0.28rem;
            position: relative;

            .good-name {
                font-size: 0.12rem;
                color: #72716f;
                position: absolute;
                top: 50%;
                transform: translate(0, -50%);
            }
        }

        .content-down {
            width: 100%;
            flex: 1;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;

            .down-left {
                font-size: 0.19rem;
                color: #e60012;
                align-self: flex-start;
                margin-top: 0.015rem;

                .symbol {
                    font-size: 0.17rem;
                    margin-right: 0.04rem;
                }
                .integral{
                    letter-spacing 0.01rem;
                    font-size: 0.13rem;
                    margin-left: 0.03rem;
                    // color #956134;
                }
            }

            .down-right {
                font-size: 0.12rem;
                text-align: center;

                .sale-word {
                    letter-spacing: 0.01rem;
                    color: #72716f;
                }

                .sale-num {
                    letter-spacing: 0.01rem;
                    margin-top: 0.03rem;
                    color: #1eb9e9;
                }
            }
        }
    }
}
</style>



