<template>
    <div ref="thisRef" class="my-order-menu">
          <div class="menu-item menu-item_0" @click="menuItemClick(0)">
              <div class="menu-text">综合</div>
              <div class="arrow-group">
                <img :src="imgRootPath+'arrow_down_active.png'" name="downArrow" class="arrow arrow-down active"/>
              </div>
          </div>
          <div class="menu-item menu-item_1" @click="menuItemClick(1)">
              <div class="menu-text">销量</div>
              <div class="arrow-group">
                <img :src="imgRootPath+'arrow_down.png'" name="downArrow" class="arrow arrow-down"/>
              </div>
          </div>
          <div class="menu-item menu-item_2" @click="menuItemClick(2)">
              <span class="menu-text">{{gtype.toString()=='2'?'积分':'价格'}}</span>
              <div class="arrow-group">
                <img :src="imgRootPath+'arrow_up.png'" name="upArrow" class="arrow arrow-up" style="top:-0.005rem;"/>
                <img :src="imgRootPath+'arrow_down.png'" name="downArrow" class="arrow arrow-down" style="top:0.005rem;"/>
              </div>
          </div>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "order-menu",
  data() {
    return {
      imgRootPath: "http://www.jubenxiong.com/img/",
      curIndex: 0
    };
  },
  props: {
    gtype: ""
  },
  components: {},
  activated() {},
  created() {},
  computed: {
    ...mapState(["openid", "path", "role"]),
    upArrowPath() {
      return this.imgRootPath + "arrow_up.png";
    },
    downArrowPath() {
      return this.imgRootPath + "arrow_down.png";
    },
    upArrowPathActive() {
      return this.imgRootPath + "arrow_up_active.png";
    },
    downArrowPathActive() {
      return this.imgRootPath + "arrow_down_active.png";
    }
  },
  methods: {
    menuItemClick(index) {
      let upArrow = this.getArrowUpDom(index);
      let downArrow = this.getArrowDownDom(index);
      if (this.curIndex == index) {
        this.reverseOrder(upArrow, downArrow);
        return;
      }
      let lastUpArrow = this.getArrowUpDom(this.curIndex);
      let lastDownArrow = this.getArrowDownDom(this.curIndex);
      if (lastUpArrow) this.darkArrow(lastUpArrow);
      this.darkArrow(lastDownArrow);
      this.curIndex = index;
      if (upArrow) {
        eval("this.$parent.sortGoodsFun" + this.curIndex + "Up()");
        this.brightArrow(upArrow);
      } else {
        this.brightArrow(downArrow);
        eval("this.$parent.sortGoodsFun" + this.curIndex + "()");
      }
    },
    reverseOrder(upArrow, downArrow) {
      //假如有upArrow，说明是双箭头，可以反序
      if (!upArrow) return;
      if (upArrow.classList.contains("active")) {
        this.darkArrow(upArrow);
        this.brightArrow(downArrow);
        eval("this.$parent.sortGoodsFun" + this.curIndex + "Down()");
      } else {
        this.brightArrow(upArrow);
        this.darkArrow(downArrow);
        eval("this.$parent.sortGoodsFun" + this.curIndex + "Up()");
      }
    },
    brightArrow(arrow) {
      //激活箭头
      if (arrow.classList.contains("active")) return;
      arrow.classList.add("active");
      let arrowName = arrow.getAttribute("name");
      arrow.src = this[arrowName + "PathActive"];
    },
    darkArrow(arrow) {
      //将将头处于非激活状态
      if (!arrow.classList.contains("active")) return;
      arrow.classList.remove("active");
      let arrowName = arrow.getAttribute("name");
      arrow.src = this[arrowName + "Path"];
    },
    getArrowUpDom(index) {
      return document.querySelector(
        ".my-order-menu .menu-item_" + index + " .arrow-up"
      );
    },
    getArrowDownDom(index) {
      return document.querySelector(
        ".my-order-menu .menu-item_" + index + " .arrow-down"
      );
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.my-order-menu {
  width: 100%;
  height: 0.31rem;
  background-color: #fff;
  display: flex;
  font-size: 0px;

  .menu-item {
    flex: 1;
    display: flex;
    align-items: center;

    .menu-text {
      flex: 1;
      letter-spacing: 0.005rem;
      font-size: 0.13rem;
      color: #000;
      text-align: right;
      margin-right: 0.02rem;
    }

    .arrow-group {
      flex: 1;
      text-align: left;
      margin-left: 0.02rem;
      display: flex;
      flex-direction: column;

      .arrow {
        width: 0.11rem;
        // height: 0.065rem;
        position: relative;
      }
    }
  }
}
</style>



